

import * as React from 'react';
import {View, Text, StyleSheet } from 'react-native';
import { themeColors } from '../constants/Colors';
import { PropsStackInstructions } from './../types';


export default function Instructions({ navigation}: PropsStackInstructions ) {
    return (
      <View style={styles.Main}>
        <Text>Instruções</Text>
       
      </View>
    );
  }
  
  const styles = StyleSheet.create({
    Main: {
      backgroundColor:themeColors.default.white,
      borderColor: themeColors.default.black,
      borderStyle: 'solid',
      borderWidth: 2,
      borderRadius: 15,
      margin: 15,
      textAlign: 'center',
      alignContent: 'center',
      justifyContent: 'center',
      flex:1,
      
    }
  });