import * as React from 'react';
import { StyleSheet } from 'react-native';

import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { RootStackParamList } from './types';

// Screens base app
import DiscBack from './screens/DiscBack';
import DiscFront from './screens/DiscFront';
import Instructions from './screens/Instructions';
import Shared from './screens/Shared';
import Credits from './screens/Credits';


// Resorcs
import NavBarContainer from './components/Nav/NavBarContainer';
import ModalDetails from './screens/ModalDetails';
import ModalTri from './screens/ModalTri';
import ModalNav from './screens/ModalNav';
import { themeColors } from './constants/Colors';



const Stack = createNativeStackNavigator<RootStackParamList>();

function App() {
  return (


    <NavigationContainer >
      <Stack.Navigator initialRouteName="DiscFront">
        <Stack.Group screenOptions={{ headerBackButtonMenuEnabled: false, headerTitleStyle: styles.title }}>
          <Stack.Screen name="DiscFront" component={DiscFront} options={{ title: "DISCO DE AMINOÁCIDOS", headerTitleStyle: styles.title }} />
          <Stack.Screen name="DiscBack" component={DiscBack} options={{ title: "TABELA DE CÓDIGO GENÉTICO", headerBackVisible: false }} />
        </Stack.Group>
        <Stack.Group screenOptions={{ presentation: 'card', headerTitleStyle: styles.title }}>

          <Stack.Screen name="ModelInfo" component={ModalDetails} options={{ title: "INFORMAÇÕES" }} />
          <Stack.Screen name="ModelTri" component={ModalTri} options={{ title: "VIZUALIZAÇÃO 3D" }} />
          <Stack.Screen name="ModalNav" component={ModalNav} options={{ title: "OPÇÕES" }} />

        </Stack.Group>
        <Stack.Group screenOptions={{ presentation: 'modal', headerTitleStyle: styles.title }}>
          <Stack.Screen name="Instructions" component={Instructions} options={{ title: "INSTRUÇÕES" }} />
          <Stack.Screen name="Shared" component={Shared} options={{ title: "COMPARTILHAR" }} />
          <Stack.Screen name="Credits" component={Credits} options={{ title: "CRÉDITOS" }} />
        </Stack.Group>
      </Stack.Navigator>
      <NavBarContainer />
    </NavigationContainer>
  );
}



const styles = StyleSheet.create({
  title: {
    color: themeColors.default.color1,
    fontWeight: 'bold'
  },
});

export default App;