import * as React from 'react';
import { useState, useEffect } from 'react';
import { StyleSheet, View, Text, Image, Button, TouchableOpacity, ImageBackground } from 'react-native';
import BtnCircle from '../components/buttons/BtnCircle';
import { PropsStackDiscFront, RootStackParamList } from './../types';
import Details from '../components/Cards/Details';
import { themeColors } from '../constants/Colors';
import { aminoacidos } from '../assets/aminoacidos';
import { FontAwesome } from '@expo/vector-icons';

const backgoundImage = require('./../assets/backgroud.png');


// Todas as imagens dos aminoacidos
const ala = require('./../assets/aminoacidos/his.png');
const arg = require('./../assets/aminoacidos/gln.png');
const asn = require('./../assets/aminoacidos/his.png');
const asp = require('./../assets/aminoacidos/gln.png');
const cys = require('./../assets/aminoacidos/his.png');
const gln = require('./../assets/aminoacidos/gln.png');
const glu = require('./../assets/aminoacidos/his.png');
const gly = require('./../assets/aminoacidos/gln.png');
const his = require('./../assets/aminoacidos/his.png');
const ile = require('./../assets/aminoacidos/gln.png');
const leu = require('./../assets/aminoacidos/his.png');
const lys = require('./../assets/aminoacidos/gln.png');
const met = require('./../assets/aminoacidos/his.png');
const phe = require('./../assets/aminoacidos/gln.png');
const pro = require('./../assets/aminoacidos/his.png');
const ser = require('./../assets/aminoacidos/gln.png');
const thr = require('./../assets/aminoacidos/his.png');
const trp = require('./../assets/aminoacidos/gln.png');
const tyr = require('./../assets/aminoacidos/his.png');
const val = require('./../assets/aminoacidos/gln.png');

const imganesAminoacidos = [ala, arg, asn, asp, cys, gln, glu, gly, his, ile, leu, lys, met, phe, pro, ser, thr, trp, tyr, val]

export default function DiscFront({ route, navigation }: PropsStackDiscFront) {

  // Controle de parametros de navegação


  React.useEffect(
    () => navigation.addListener('focus', () => {
      setCount(0)
    }
    ),
    []
  );


  const [count, setCount] = useState(0);

  // Função Referencia
  let openModalInfo = () => {
    navigation.navigate('ModelInfo', { aminoAcid: count }) // open drawer
  };

  let openModalTri = () => {
    navigation.navigate('ModelTri', { aminoAcid: count }) // open drawer
  };

  let openModalCodon = () => {
    navigation.navigate('DiscBack', { aminoAcid: count }) // open drawer
  };

  let gabarito = require('./../assets/gabarito.png');
  let base = require('./../assets/base.png');
  let basebranca = require('./../assets/basebranca.png');

  const { name, info } = aminoacidos(count)

  console.log(count);
  function next() {
    if (count === 19)
      setCount(0)
    else
      setCount(count + 1)
  }

  function prev() {
    if (count === 0)
      setCount(19)
    else
      setCount(count - 1)
  }

  return (
    <ImageBackground source={backgoundImage} resizeMode="cover" style={{
      flex: 1,
      justifyContent: "center"
    }}>
      <View style={styles.Main}>

        <View style={stylesDisc.container}>

          <View style={stylesDisc.containerTitle}>
            <View style={stylesDisc.display}>
              <Text style={stylesDisc.title}>{name}</Text>
            </View>
          </View>

          <View style={stylesDisc.containerDisc}>


            <View style={stylesDisc.imagens}>
              <Image source={base} style={stylesDisc.base} resizeMode='contain' />
              <Image source={basebranca} style={stylesDisc.basebranca} resizeMode='contain' />

              <View style={stylesDisc.containerAminoacidoImage}>
                <Image source={imganesAminoacidos[count]} style={stylesDisc.aminoacidoImage} resizeMode='contain' />

              </View>
              <Image source={gabarito} style={stylesDisc.gabarito} resizeMode='contain' />

            </View>


            <View style={stylesDisc.containerControl}>
              <View style={stylesDisc.control}>
                <TouchableOpacity
                  onPress={() => prev()}
                  style={stylesButton.btn}
                >
                  <View style={[{ borderTopWidth: 5 }, stylesButton.iconContainer]}>
                    <View style={stylesButton.icon}>
                      <FontAwesome

                        name="arrow-up"
                        style={
                          stylesButton.iconFont
                        }

                      />
                    </View>
                  </View>
                </TouchableOpacity>
                <Text>
                  <FontAwesome

                    name="caret-left"
                    style={
                      stylesButton.iconFont
                    }

                  />
                </Text>
                <TouchableOpacity
                  onPress={() => next()}
                  style={stylesButton.btn}
                >
                  <View style={[{ borderBottomWidth: 5 }, stylesButton.iconContainer]}>
                    <View style={stylesButton.icon}>
                      <FontAwesome

                        name="arrow-down"
                        style={
                          stylesButton.iconFont
                        }

                      />
                    </View>
                  </View>
                </TouchableOpacity>
              </View>
            </View>
          </View>


        </View>

        <View style={styles.Buttons}>
          <BtnCircle
            icon="info"
            name="INFORMAÇÃO"
            onPress={() => openModalInfo()}
          />

          <BtnCircle
            icon="cubes"
            name="VIZUALIZAÇÃO"
            onPress={() => openModalTri()}
          />

          <BtnCircle
            icon="circle"
            name="CÓDON"
            onPress={() => openModalCodon()}
          />
        </View>

      </View>
    </ImageBackground>
  );
}
const styles = StyleSheet.create({
  Main: {
    flex: 1,
    padding: 15,

    justifyContent: 'space-evenly',
    flexDirection: 'column'
  },
  Disc: {
    flexGrow: 1
  },
  Buttons: {
    flexDirection: 'row',
    justifyContent: 'space-around',
  }
});

const stylesButton = StyleSheet.create({
  btn: {
    alignItems: "center",
  },
  icon: {
    width: "100%",
    height: "100%",

    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',




    alignItems: "center",

  },
  iconFont: {
    color: themeColors.default.white,
    fontSize: 32
  },
  iconContainer: {
    backgroundColor: themeColors.default.color1,
    color: themeColors.default.white,
    width: 46,
    height: 46,
    borderRadius: 50,

    borderColor: themeColors.default.scolor2,
    borderWidth: 1,



  }
});

const stylesDisc = StyleSheet.create({
  container: {
    flex: 1,
    flexDirection: 'column',

  },
  containerTitle: {
    backgroundColor: themeColors.default.color1,
    borderRadius: 10,
    padding: 10,

  },
  containerDisc: {
    flex: 1,
    flexGrow: 1,
    flexDirection: 'row',
  },

  display: {
    backgroundColor: themeColors.default.white,
    padding: 5,
    borderRadius: 10
  },
  title: {
    fontSize: themeColors.default.title2,
    textAlign: "center",
    fontWeight: 'bold'
  },

  imagens: {
    flexGrow: 1,
    justifyContent: "center",
    alignItems: "center",
    position: "relative"
  },
  gabarito: {
    width: "100%",
    height: "100%",
    position: 'absolute',
    zIndex: 3
  },
  aminoacidos: {
    width: "98%",
    height: "98%",
    position: 'absolute',
    zIndex: 2,
    borderRadius: 50

  },
  containerAminoacidoImage: {
    flexGrow: 1,
    justifyContent: "center",
    alignItems: "flex-end",
    position: "relative",
    width: "100%",
    height: "100%",
    zIndex: 2,

  },
  aminoacidoImage: {
    width: "70%",
    height: "33%",
    right: 5,
    position: 'absolute',
    zIndex: 2,
  }
  ,
  base: {
    width: "100%",
    height: "100%",
    position: 'absolute',
    zIndex: 1

  },
  basebranca: {
    width: "97%",
    height: "97%",
    position: 'absolute',
    zIndex: 1

  },

  containerControl: {
    justifyContent: 'center',
    marginLeft: 10,
  },
  control: {
    justifyContent: 'space-evenly',
    backgroundColor: themeColors.default.color1,
    height: 250,
    padding: 5,
    borderRadius: 10,
  },



});