import * as React from 'react';
import { View, Text, StyleSheet } from 'react-native';
import { PropsStackModelInfo } from '../types';
import { themeColors } from '../constants/Colors';
import { aminoacidos } from '../assets/aminoacidos';

console.log(aminoacidos(1).info[0]);


export default function ModalDetails({ route, navigation }: PropsStackModelInfo) {


  const { aminoAcid } = route.params;
  const { name, info } = aminoacidos(aminoAcid)
  console.log("INFO", aminoAcid);

  return (
    <View style={styles.container}>
      <View style={styles.text}>
        <Text style={styles.name}>{name}</Text>
        <View style={styles.descrebes}>
          <Text style={styles.descrebePrimary}>{info[0]}</Text>
          <Text style={styles.descrebeSecundary}>{info[1]}</Text>
        </View>

      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'space-between',
    padding: 15,
    backgroundColor: themeColors.default.white,
    borderRadius: 10,
    textAlign: 'center'
  },
  name: {
    textAlign: 'center',
    padding: 15,
    backgroundColor: themeColors.default.color1,
    fontSize: themeColors.default.title2,
    color: themeColors.default.white,
    fontWeight: 'bold',
    borderRadius: 15,
    // borderStyle: 'solid',
    // borderColor: themeColors.default.black,
    // borderWidth: 2
  },
  descrebePrimary: {
    color: themeColors.default.black,
    fontSize: themeColors.default.par2

  },
  descrebeSecundary: {
    backgroundColor: themeColors.default.color2,
    color: themeColors.default.black,
    fontSize: themeColors.default.par2,
    padding: 10,
    paddingTop: 25,
    marginTop: 20,
    paddingBottom: 25,
    borderRadius: 10
  },
  text: {
    flexGrow: 1,
    flex: 1,
    justifyContent: 'space-evenly'
  },
  descrebes:{
    marginTop: 20,
    flexGrow:1,
    justifyContent: 'flex-start'
  } 
});
