import React from 'react';
import { FontAwesome } from '@expo/vector-icons';
import { themeColors } from '../../constants/Colors';
import { StyleSheet, Text, View, TouchableOpacity } from 'react-native';
import { PropsButtuns } from '../../types';

function BtnRectangle(props: PropsButtuns) {

  // Função ancora
  let onPress = () => {
    props.onPress();
  };

  return (
    <TouchableOpacity
      onPress={() => onPress()}
      style={styles.BtnRectangle}
    >
      <View style={styles.iconContainer}>
        <View style={styles.icon}>
          <FontAwesome
   
            name={props.icon}
            style={
              styles.iconFont
            }

          />
        </View>
      </View>
      <View style={styles.containerTitle}>
        <Text style={styles.title}>{props.name}</Text>
        
      </View>
    </TouchableOpacity>



  );
}


const styles = StyleSheet.create({
  BtnRectangle: {
    flex:1,
    height: 56,
    justifyContent: 'center',
    flexDirection: 'row',
    alignItems: "center",
  },
  icon: {
    width: "100%",
    height: "100%",

    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',




    alignItems: "center",

  },
  iconFont: {
    color: themeColors.default.white,
    fontSize: 24
  },
  iconContainer: {
      marginRight: 5,
    backgroundColor: themeColors.default.color1,
    color: themeColors.default.white,
    width: 56,
    height: 56,
    borderRadius: 10,
    elevation: 4,
    shadowColor: 'black',
    shadowOffset: { width: 0, height: 4 },
    shadowOpacity: 0.25,
    shadowRadius: 4,
    
  },
  containerTitle :{
    marginLeft: 5, 
    borderColor: themeColors.default.color1,
    borderWidth: 2,
    borderRadius: 10,
    width: 200,
    height: 56,
    alignItems: 'center',
    justifyContent: 'center'

  }
  , title: {
    fontSize: themeColors.default.title3,
    color: themeColors.default.color1

  }
});

export default BtnRectangle;