import * as React from 'react';
import { View, Text, StyleSheet, Image } from 'react-native';
import { PropsStackModelTri } from '../types';
import  {themeColors}  from '../constants/Colors';
import { aminoacidos } from '../assets/aminoacidos';

console.log(aminoacidos(1).info[0]);


export default function ModalTri( {route, navigation}: PropsStackModelTri) {

    let model = require('./../assets/ala.gif');
  const { aminoAcid } = route.params;
  const {name, info} = aminoacidos(aminoAcid)
  console.log("INFO",aminoAcid);
  
    return (
      <View style={styles.container}>
          <Text style={styles.name}>{name}</Text>
            <View style={styles.containerImage}>
                <Image source={model} style={styles.image} resizeMode ='contain' /> 
            </View>
        </View>
    );
  }
  
  const styles = StyleSheet.create({
    container: {
      flex: 1,
      justifyContent: 'space-between',
      padding: 15,
      backgroundColor:themeColors.default.white,
      borderRadius: 10,
      textAlign: 'center'
    },
    name:{
      textAlign: 'center',
      padding: 13,
      backgroundColor:themeColors.default.color1,
      fontSize:themeColors.default.title2,
      color: themeColors.default.white,
      fontWeight: 'bold',
      borderRadius:10,
      borderStyle: 'solid',
      borderColor: themeColors.default.black,
      borderWidth:2
    },
    containerImage:{ 
        padding:10,
        flexGrow:1
        

    }, 
    image:{
        padding: 10,
        borderRadius: 10,

        height: '100%',
        width:'100%'
        
    }
  });
