import React from 'react';
import { FontAwesome } from '@expo/vector-icons';
import { themeColors } from '../../constants/Colors';
import { StyleSheet, Text, View, TouchableOpacity } from 'react-native';
import { PropsButtunsflats } from '../../types';

function BtnFlatBlackWhite(props: PropsButtunsflats) {

  // Função ancora
  let onPress = () => {
    props.onPress();
  };

  return (
    <TouchableOpacity
      onPress={() => onPress()}
      style={styles.btnCircle}
    >
      <View style={styles.iconContainer}>
        <View style={styles.icon}>
          <FontAwesome

            name={props.icon}
            style={
              styles.iconFont
            }

          />
        </View>
      </View>
    </TouchableOpacity>



  );
}


const styles = StyleSheet.create({
  btnCircle: {
    alignItems: "center",
  },
  icon: {
    width: "100%",
    height: "100%",

    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',




    alignItems: "center",

  },
  iconFont: {
    color: themeColors.default.black,
    fontSize: 24
  },
  iconContainer: {
    backgroundColor: themeColors.default.white,
    color: themeColors.default.black,
    width: 56,
    height: 56,
    borderRadius: 50,
    elevation: 4,
    shadowColor: 'black',
    shadowOffset: { width: 0, height: 4 },
    shadowOpacity: 0.25,
    shadowRadius: 4,
    
  }
});

export default BtnFlatBlackWhite;