

import React, { useState } from 'react';
import { View, StyleSheet } from 'react-native';
import BtnRectangle from '../components/buttons/BtnRectangle';
import { useLinkTo } from '@react-navigation/native';
import { themeColors } from '../constants/Colors';





export default function ModalNav() {
  const link = useLinkTo ();


  // Função Referencia
  let openHelp = () => {

      link('/Instructions') // open drawer
  };
  let openCredits = () => {
      link('/Credits') // open drawer
  };
  let openShared = () => {
      link('/Shared') // open drawer
  };
    return (
      <View style={styles.Opcoes}>
        
        <View style= {styles.item}>
            <BtnRectangle
            icon={"question"}
            key={"ajuda"}
            name={"AJUDA"}
            onPress ={() => openHelp()}            
            />
        </View>
        <View style= {styles.item}>
            <BtnRectangle
            icon={"group"}
            key={"creditos"}
            name={"CRÉDITO"}
            onPress ={() => openCredits()}            
            />
        </View>
        <View style= {styles.item}>
            <BtnRectangle
            icon={"share-alt"}
            key={"compartilhar"}
            name={"COMPARTILHAR"}
            onPress ={() => openShared()}            
            />
        </View>
      </View>
    );
  }
  

  const styles = StyleSheet.create({
    Opcoes: {
      backgroundColor:themeColors.default.white,
      borderColor: themeColors.default.black,
      borderStyle: 'solid',
      borderWidth: 2,
      borderRadius: 15,
      flex:1,
      padding: 45,
      margin: 15,
      alignItems: 'center',
      justifyContent: 'space-around'
    },
    item :{
      width: "100%"
    }
  });
  