

import React, { useCallback, useRef } from 'react';
import { useState, useEffect } from 'react';
import { StyleSheet, View, Text, Image, Button, TouchableOpacity, Animated, ImageBackground } from 'react-native';
import BtnCircle from '../components/buttons/BtnCircle';
import { PropsStackDiscBack, RootStackParamList } from './../types';
import { themeColors } from '../constants/Colors';
import { aminoacidos } from '../assets/aminoacidos';
import { FontAwesome } from '@expo/vector-icons';

const backgoundImage =require('./../assets/backgroud.png');


export default function DiscBack({ route, navigation }: PropsStackDiscBack) {

  // Controle de parametros de navegação
  
   const aminoAcid = route.params.aminoAcid;


  const [count, setCount] = useState(aminoAcid);

  // Função Referencia
  let openModalInfo = () => {
    navigation.navigate('ModelInfo', { aminoAcid: count }) // open drawer
  };

  let openModalTri = () => {
    navigation.navigate('ModelTri', { aminoAcid: count }) // open drawer
  };

  let openModalFront = () => {
    navigation.navigate('DiscFront', { aminoAcid: count }) // open drawer
  };

  console.log(count);
  



  let aminoacidosDisc = require('./../assets/discoverso.png');
  let base = require('./../assets/base.png');

  const { name, info } = aminoacidos(count)

  // Animação rotação
  function transition(value:number) {
    const pontos = [190,325,263,175,62,130,145,165,343,355,306,34,251,295,12,241,85,280,48,213]
    return {
      transform: [{ rotate: `${pontos[value]}deg`}]
    }
  }
 

  function next() {
    if (count === 19)
      setCount(0)
    else
      setCount(count + 1)
  }

  function prev() {
    if (count === 0)
      setCount(19)
    else
      setCount(count - 1)
  }

  return (
<ImageBackground source={backgoundImage} resizeMode="cover" style={{
    flex: 1,
    justifyContent: "center"
  }}>
    <View style={styles.Main}>
      <View style={stylesDisc.container}>

        <View style={stylesDisc.containerTitle}>
          <View style={stylesDisc.display}>
            <Text style={stylesDisc.title}>{name}</Text>
          </View>
        </View>

        <View style={stylesDisc.containerDisc}>


          <View style={stylesDisc.imagens}>
            <Image source={base} style={stylesDisc.base} resizeMode='contain' />
            <Image source={aminoacidosDisc} style={[transition(count),stylesDisc.aminoacidos]} resizeMode='contain' />
          </View>


          <View style={stylesDisc.containerControl}>
            <View style={stylesDisc.control}>
              <TouchableOpacity
                onPress={() => prev()}
                style={stylesButton.btn}
              >
                <View style={[{ borderTopWidth: 5 }, stylesButton.iconContainer]}>
                  <View style={stylesButton.icon}>
                    <FontAwesome

                      name="arrow-up"
                      style={
                        stylesButton.iconFont
                      }

                    />
                  </View>
                </View>
              </TouchableOpacity>
              <Text>
                <FontAwesome

                  name="caret-left"
                  style={
                    stylesButton.iconFont
                  }

                />
              </Text>
              <TouchableOpacity
                onPress={() => next()}
                style={stylesButton.btn}
              >
                <View style={[{ borderBottomWidth: 5 }, stylesButton.iconContainer]}>
                  <View style={stylesButton.icon}>
                    <FontAwesome

                      name="arrow-down"
                      style={
                        stylesButton.iconFont
                      }

                    />
                  </View>
                </View>
              </TouchableOpacity>
            </View>
          </View>
        </View>


      </View>

      <View style={styles.Buttons}>
        <BtnCircle
          icon="info"
          name="INFORMAÇÃO"
          onPress={() => openModalInfo()}
        />

        <BtnCircle
          icon="cubes"
          name="VIZUALIZAÇÃO"
          onPress={() => openModalTri()}
        />

        <BtnCircle
          icon="circle"
          name="AMINOÁCIDOS"
          onPress={() => openModalFront()}
        />
      </View>

    </View>
    </ImageBackground>
  );
}
const styles = StyleSheet.create({
  Main: {
    flex: 1,
    margin: 15,

    justifyContent: 'space-evenly',
    flexDirection: 'column'
  },
  Disc: {
    flexGrow: 1
  },
  Buttons: {
    flexDirection: 'row',
    justifyContent: 'space-around',
  }
});

const stylesButton = StyleSheet.create({
  btn: {
    alignItems: "center",
  },
  icon: {
    width: "100%",
    height: "100%",

    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',




    alignItems: "center",

  },
  iconFont: {
    color: themeColors.default.white,
    fontSize: 32
  },
  iconContainer: {
    backgroundColor: themeColors.default.color1,
    color: themeColors.default.white,
    width: 46,
    height: 46,
    borderRadius: 50,

    borderColor: themeColors.default.scolor2,
    borderWidth: 1,



  }
});

const stylesDisc = StyleSheet.create({
  container: {
    flex: 1,
    flexDirection: 'column',

  },
  containerTitle: {
    backgroundColor: themeColors.default.color1,
    borderRadius: 10,
    padding: 10,

  },
  containerDisc: {
    flex: 1,
    flexGrow: 1,
    flexDirection: 'row',
  },

  display: {
    backgroundColor: themeColors.default.white,
    padding: 5,
    borderRadius: 10
  },
  title: {
    fontSize: themeColors.default.title2,
    textAlign: "center",
    fontWeight:'bold'
  },

  imagens: {
    flexGrow: 1,
    justifyContent: "center",
    alignItems: "center",
    position: "relative"
  },
  aminoacidos: {
    width: "98%",
    height: "98%",
    position: 'absolute',
    zIndex: 1,
    borderRadius: 50,

  },
  base: {
    width: "100%",
    height: "100%",
    position: 'absolute',
    zIndex: 1

  },

  containerControl: {
    justifyContent: 'center',
    marginLeft: 10,
  },
  control: {
    justifyContent: 'space-evenly',
    backgroundColor: themeColors.default.color1,
    height: 250,
    padding: 5,
    borderRadius: 10,
  },



});