

import * as React from 'react';
import { Button, View, Text, StyleSheet } from 'react-native';
import { themeColors } from '../../constants/Colors';
import BtnFlatColor from '../buttons/BtnFlatColor';


export default function follow() {
    return (
        <View style={styles.Main}>
            <View style={styles.containerText}>
                <Text style={styles.text}>Compartilhe o <Text style={styles.destaque}>Disco de Aminoácidos</Text> em suas mídias sociais</Text>
            </View>

            <View style={styles.icons}>
                <BtnFlatColor color='#3B5998' icon={'facebook'} key={1} onPress={() => null} />
                <BtnFlatColor color='#189D0E' icon={'whatsapp'} key={1} onPress={() => null} />
                <BtnFlatColor color='#CE098F' icon={'instagram'} key={1} onPress={() => null} />
                <BtnFlatColor color='#0088CC' icon={'twitter'} key={1} onPress={() => null} />
            </View>

        </View>

    );
}

const styles = StyleSheet.create({
    Main: {
        padding: 15,
        textAlign: 'center',
        alignContent: 'center',
        justifyContent: 'space-evenly',
        flex: 1
    },
    icons: {
        flex: 1,
        flexDirection: 'row',
        justifyContent: 'space-between'


    }, containerText: {
        flex: 2,

        flexDirection: 'column',
        justifyContent: 'center'



    },
    text: {
        fontSize: themeColors.default.title3,
    },
    destaque: {
        color: themeColors.default.color1,
        fontWeight: 'bold'


    }
});
