

import * as React from 'react';
import { Button, View, Text, StyleSheet } from 'react-native';
import { themeColors } from '../../constants/Colors';
import BtnFlatBlackWhite from '../buttons/BtnFlatBlackWhite';


export default function Networks() {
  return (
    <View style={styles.Main}>
      <View style={styles.containerText}>
        <Text style={styles.text}>SIGA O EIC</Text>
        <Text>
        Interaja com a gente nas mídias sociais e em nosso site!
        </Text>
      </View>


      <View style={styles.icons}>
          <BtnFlatBlackWhite color='white' icon={'facebook'} key={1} onPress = {() => null}/>
          <BtnFlatBlackWhite color='white' icon={'whatsapp'} key={1} onPress = {() => null}/>
          <BtnFlatBlackWhite color='white' icon={'instagram'} key={1} onPress = {() => null}/>
          <BtnFlatBlackWhite color='white' icon={'twitter'} key={1} onPress = {() => null}/>
      </View>

    </View>
    
  );
}

const styles = StyleSheet.create({
  Main: {
    padding: 15,
    textAlign: 'center',
    alignContent: 'center', 
    justifyContent: 'space-evenly',
    
    flex:1
  },
  icons:{
      flex: 1,
      flexDirection: 'row',
      justifyContent: 'space-between'


  }, containerText:{
      flex: 2,
      
      flexDirection: 'column',
      justifyContent: 'center'
  },
  text:{
    fontSize: themeColors.default.title1,
    color: themeColors.default.white,
    fontWeight: 'bold'
  },
  textsecundary:{
    fontSize: themeColors.default.title3,
    color: themeColors.default.black,
    fontWeight: 'bold'
  },
  destaque:{
      color: themeColors.default.color1,
      fontWeight: 'bold'


  }
});
